.app-wrapper {
  width: 1200px;
  min-height: 979px;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "h h " "n c ";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.wrapper__content {
  grid-area: c;
  background-color:#B8B799;
}